export const resolveImg = (url: string) =>
  url === 'pharma'
    ? pharma
    : url === 'nutrition'
    ? nutrition
    : url === 'supplement'
    ? supplement
    : url;

export const pharma =
  'data:image/jpeg;base64,';
export const nutrition =
  'data:image/jpeg;base64,';
export const supplement =
  'data:image/jpeg;base64,';
